@import "../../assets/css/Components.scss";
.section-service{
    @extend .d-flex;
    justify-content: space-between;
    width:100%;
        @media(max-width:$ipad){
            flex-wrap: wrap;
        }
    .list-item{
        width:25%;       
        @media(max-width:$ipad){
            width:50%;
        }
        background-color: $gray-400;
        position: relative;
        &:nth-child(2){
            background-color: $gray-500;
        }
        &:nth-child(3){
            background-color: $gray-600;
        }
        &:nth-child(4){
            background-color: $gray-700;
        }
        img{
            width:100%;
            height:100%;
            object-fit: cover;
        }
        .heading-top{
            position:absolute;
            top:35px;
            left:25px;
            z-index: 2;
            @media(max-width:540px){
                top:4vw;
                left:3vw;
            }  
            h4{
                color:$black;
                text-transform: uppercase;
                font-size:2rem;
                letter-spacing: 1.25px;
                margin-bottom:.4rem;
                transition: color .3s ease-in-out;
                span{
                    display: block;
                }  
                @media(max-width:1368px){
                    font-size:1rem;
                }   
                @media(max-width:1200px){
                    font-size:1.5rem;
                }  
                @media(max-width:540px){
                    font-size:3vw;
                }               
            }
            &.white{
               h4{
                color:$white;
               }
            }
        }
        .read-more{
            position:absolute;
            bottom:55px;
            left:55px;
            z-index: 2;
            color:$white;
            text-transform: uppercase;
            font-size:.85rem;
            letter-spacing: 1.25px;
            font-weight: 600;
            transition: color .3s ease-in-out;
        }
        &:before{
            position: absolute;
            width: 0; 
            height: 100%; 
            background-color: rgba($white, .7);
            left: 0;
            top: 0;
            content: "";
            z-index: 1;
            transition: width 0.3s ease-in-out;
        }
        &:hover{
            cursor: pointer;
            &:before{
                width: 100%;
            }
            .heading-top{
                .service-title{
                    color:$black;
                }
                h4{
                    color:$black;
                }
            }
            .read-more{
                color:$black;
            }
        }
    }
}

.slider-container{
    background-color: $gray-200;
    .item-container{
        text-align: center;
        @media(max-width:1200px){
            padding:2rem;
        }
        .item{
            text-align: center;
            @extend .d-flex;
            flex-direction: column;
            padding:2rem;
            @media(max-width:1200px){
                padding:12px;
            }
            .icon{
                width:100px;
                height:100px;
                margin-bottom:1rem;
                img{
                    opacity:.6;
                    transition:all .3s ease-in-out;
                }
                @media(max-width:1368px){
                    width:80px;
                    height:80px;
                    margin-bottom:14px;
                }

            }
            .item-title{
                font-size:1rem;
                text-transform: uppercase;
                letter-spacing: 1.25px;
                color:$black;
                @media(max-width:1368px){
                    font-size:12px;
                }
            }
            &:hover{
                .item{
                    .icon{
                       img{
                        opacity:1;                        
                       }
                    }
                }
            }
        }
    }
}

.sliderContainer{
    .item-container{
        .item{
            img{
                display: block;
                width:100%;
            }
        }
    }
}